const accessTokenMatch = window.location.hash.match(/^#access_token=([^&]+).+$/)
const accessToken = accessTokenMatch?.[1]
const divContent = document.body.querySelector('.content')
if (divContent) {
	const link = document.createElement('a')
	link.href = accessToken ? `baseg-chat://authentication/${accessToken}` : '/'
	link.innerHTML = accessToken ? '<h1>Continue to Baseg Chat</h1>' : '<h1>Baseg Chat</h1>'
	divContent.appendChild(link)
}
if (accessToken) {
	window.location.replace(`baseg-chat://authentication/${accessToken}`)
} else {
	window.location.replace('/')
}
